import React, { Component } from 'react';

import Row from './Row';
import Cell from './Cell';

import DeviceTasks from './device/Tasks';

class Tasks extends Component {
  render() {
    return (
      <section>
        <Row>
          <Cell>
            <h2>Kilpaile perheen kesken suorittamalla tehtvi</h2>
            <p>Ker henkilkohtaisia pisteit koiran hoitamisesta: mit vaativampi tai aikaavievmpi tehtv on, sit enemmn pisteit saat!</p>
            <p>Pisteet toimivat rahayksikkin Dogtivityn sisisess hauskassa peliosiossa, jossa etenkin perheen nuorimmaiset voivat luoda oman virtuaalikoiran ja ostella sille erilaisia tarvikkeita. Pisteiden avulla perheenjsenet mys nkevt kuinka tasapuolisesti hoitotoimenpiteet oikeassa elmss jakautuvat.</p>
          </Cell>
          <Cell>
            <DeviceTasks />
          </Cell>
        </Row>
      </section>
    );
  }
}

export default Tasks;



