import React, { Component } from 'react';

import Row from './Row';
import Cell from './Cell';

import DeviceReminder from './device/Reminder';

class Reminder extends Component {
  render() {
    return (
      <section>
        <Row>
          <Cell>
            <h2>Pysy ajan tasalla hoitorutiineista</h2>
            <p>Pid kirjaa koirasi snnllisist hoitotoimenpiteist kuten rokotuksista ja madotuksista. Dogtivity muistuttaa ajankohtien lhestyess ja saat ktevsti varattua koirallesi mys lkriajan.</p>
            <p>Saat muistutuksia mys muista koirasi hoitorutiineista kuten kynsienleikkuusta ja turkinhoidosta. Voit muokata tehtvt ja muistutukset vastaamaan sinun ja koirasi yksilllisi aikatauluja ja tarpeita.</p>
          </Cell>
          <Cell>
            <DeviceReminder />
          </Cell>
        </Row>
      </section>
    );
  }
}

export default Reminder;



